/**
 * @module mod_psgrading/post
 */
define(['jquery', 'mod_psgrading/recipientselector', 'core/log', 'core/templates', 'core/form-autocomplete'], 
    function($, RecipientSelector, Log, Templates, AutoComplete) {    
    'use strict';

    /**
     * Initializes the post component.
     */
    function init() {
        Log.debug('mod_psgrading/post: initializing');

        var rootel = $('#page-local-psgrading-post');

        if (!rootel.length) {
            Log.error('mod_psgrading/post: #page-local-psgrading-post not found!');
            return;
        }

        var post = new Post(rootel);
        post.main();
    }

    /**
     * The constructor
     *
     * @constructor
     * @param {jQuery} rootel
     */
    function Post(rootel) {
        var self = this;
        self.rootel = rootel;
    }

    /**
     * Run the Audience Selector.
     *
     */
   Post.prototype.main = function () {
        var self = this;

        // Initialise the recipient selector.
        var types = ['user', 'course', 'group', 'board'];
        var roles = ['student'];
        RecipientSelector.init(types, roles);

    };

    return {
        init: init
    };
});